.container {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 50px 0;
  }

.card {
    margin: 0 1% !important;
}

.infected {
    border-bottom: 10px solid rgba(0, 0, 255, 0.5);
}

.recovered {
    border-bottom: 10px solid rgba(64, 204, 87, 0.5);
}

.deaths {
    border-bottom: 10px solid rgba(156, 60, 100, 0.5);
}